.custom-dropdown-container {
  position: relative;
  width: 300px;
}

.dropdown-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
  cursor: pointer;
  width: 171%;
}

.dropdown-icon {
  margin-left: auto;
}

.dropdown-list-new {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  border: 1px solid #ccc;
  border-top: none;
  background-color: white;
  max-height: 200px;
  overflow-y: auto;
  z-index: 1000;
  /* display: none; */
}

.dropdown-list-new.open {
  display: block;
}

.dropdown-option {
  padding: 10px;
  cursor: pointer;
}

.dropdown-option:hover {
  background-color: #f0f0f0;
}

.dropdown-option.selected {
  background-color: #e0e0e0;
  font-weight: bold;
}

.dropdown-option:last-child {
  border-top: 1px solid #ccc;
}

.dropdown-option:first-child {
  border-bottom: 1px solid #ccc;
}
